.navbar {
  background:#011638;
  /* box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 7.0px );
  -webkit-backdrop-filter: blur( 7.0px ); */
  /* border: 1px solid rgba( 255, 255, 255, 0.18 ); */
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  color: white;
  top: 0;
  z-index: 999;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
 }
 
 .navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 1500px;
 }
 
 .navbar-logo {
  font-family: 'IBM Plex Sans', sans-serif;
  font-style: italic;
  letter-spacing: 1px;
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  white-space: nowrap;
 }
 
 .fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
 }
 
 .nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;
 }
 
 .nav-item {
  height: 80px;
 }
 
 .nav-links {
  color: #EFEFD0;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  display: none;
 }
 
 .fa-bars {
  color: #EFEFD0;
 }
 
 
 .nav-links-mobile {
  display: none;
 }
 
 .menu-icon {
  display: none;
  color: #66fcf1;
 }
 
 @media screen and (max-width: 1500PX) {
  .navbar-container {
    max-width: 1000px;
   }
 }

 @media screen and (max-width: 500px) {
  .navbar-logo {
    letter-spacing: 1px;
    justify-self: left;
    margin-left: -30px;

   }
}
 

 @media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }
 
  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }
 
  .nav-menu.active {
    background: #011638;
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 7.0px );
    -webkit-backdrop-filter: blur( 7.0px );
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }
 
  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }
 
  .nav-links:hover {
    border-color: #66fcf1 ;
    border-width: 20px;
    background-color: #EFEFD0;
    color: #242424;
    border-radius: 0;
  }
 
  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }
 
  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }
 
  .fa-times {
    color: #EFEFD0;
    font-size: 2rem;
  }
 
  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #EFEFD0;
    padding: 14px 20px;
    border: 1px solid #EFEFD0;
    transition: all 0.3s ease-out;
  }
 
  .nav-links-mobile:hover {
    border: #66fcf1;
    background: #EFEFD0;
    color: #242424;
    transition: 250ms;
  }
 }