*, ::before, ::after {
  box-sizing: border-box;
}

.content2 {
  line-height: 40px;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  width: 40%;
  padding: 1em;
  text-align: center;
  border-radius: 0.5em;
  background-color: #FF6B35;
  /* border: 12px solid rgba(91, 65, 242, 0.5); */
  color: #ffffff;
  /* mix-blend-mode: luminosity; */
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
}
.content2 h1 {
  margin: 20px;
  font-size: 40px;
  letter-spacing: 5px;
  color: #EFEFD0;
  font-weight: 400;
  /* font-family: 'IBM Plex Sans', sans-serif; */
}

.container h2 {
  letter-spacing: 5px;
  font-size: 20px;
  padding: 30px 20px;
  font-weight: 500;
}
.content2 a {
  text-decoration: none;
  color: #ab49de;
}

.container {
  height: 100vh;
}
.container {
  position: relative;
  width: 100%;
  overflow: hidden;
  background-color: #EFEFD0;
}
.container::before {
  position: absolute;
  transform: translateY(50%);
  top: 60px;
  width: 350px;
  height: 350px;
  content: "";
  border-radius: 54% 46% 42% 58% / 60% 58% 42% 40%;
  background-image: linear-gradient(45deg, #FF6B35, #F7C59F);
  animation: vawe 5s linear infinite;
}

.container::after {
  position: absolute;
  top: 500px;
  left: 20px;
  width: 350px;
  height: 350px;
  content: "";
  border-radius: 54% 46% 42% 58% / 60% 58% 42% 40%;
  background-image: linear-gradient(45deg, #FF6B35, #F7C59F);
  animation: vawe 5s linear infinite;
}
.container::before {
  top: -10%;
  right: -25%;
}
.container::after {
  bottom: -10%;
  left: -15%;
}
.container .dots {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.container .dots span {
  position: absolute;
  border-radius: 50%;
  background-image: linear-gradient(45deg, #FF6B35, #F7C59F);
  border-radius: 38% 62% 55% 45% / 32% 53% 47% 68%;
  animation: vawe 7s linear infinite;
}
.container .dots span:nth-child(1) {
  top: 10%;
  left: calc(100% - 360px);
  width: 75px;
  height: 75px;
}
.container .dots span:nth-child(2) {
  top: 15px;
  left: 180px;
  width: 50px;
  height: 50px;
  border-radius: 38% 62% 33% 67% / 60% 53% 47% 40%;
  transform: rotate(90deg);
}
.container .dots span:nth-child(3) {
  right: 180px;
  bottom: 20px;
  width: 80px;
  height: 80px;
  border-radius: 38% 62% 55% 45% / 52% 53% 47% 48%;
}
.container .dots span:nth-child(4) {
  bottom: 50px;
  left: 240px;
  width: 20px;
  height: 20px;
  border-radius: 38% 62% 55% 45% / 52% 53% 47% 48%;
}
.container .dots span:nth-child(5) {
  right: 280px;
  bottom: 80px;
  width: 25px;
  height: 25px;
  border-radius: 38% 62% 55% 45% / 52% 53% 47% 48%;
}
.container .dots span:nth-child(6) {
  top: 6%;
  left: calc(100% - 400px);
  width: 25px;
  height: 25px;
}

@keyframes vawe {
  20% {
    border-radius: 45% 55% 62% 38% / 53% 51% 49% 47%;
 }
  40% {
    border-radius: 45% 55% 49% 51% / 36% 51% 49% 64%;
 }
  60% {
    border-radius: 60% 40% 57% 43% / 47% 62% 38% 53%;
 }
  80% {
    border-radius: 60% 40% 32% 68% / 38% 36% 64% 62%;
 }
}

@media screen and (max-width: 1100px) {
  .content2 {
    width: 70%;
  };
}

@media screen and (max-width: 600px) {
  .content2 {
    width: 80%;
  }
  .container h2 {
    letter-spacing: 0px;
    font-size: 8px;
    padding: 10px 20px;
  }
  .content2 h1 {
    margin: 5px;
    font-size: 20px;
    letter-spacing: 0px;
    color: #EFEFD0;
  }
}
